import { Col, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import { useEffect, useState } from 'react';
import { useNavigate } from "react-router";
import {useSelector} from 'react-redux';
import axios from 'axios';
import { url } from '../config';
const BoardWrite = () => {
    const divStyle = {margin:"0 auto", width:"600px", border: "1px solid lightgray", borderRadius:"7px", padding: '10px'}
    const navigate = useNavigate();
    const [fileList, setFileList] = useState([]);
    const user = useSelector(state=>state.persistedReducer.user);
    const [board, setBoard] = useState({subject:'', content:'', writer:user.id})
    

    useEffect(()=>{
        let loginUser = JSON.parse(sessionStorage.getItem("user"));
        loginUser && setBoard({...board, writer:loginUser.id});
    }, [])
    const fileChange = (e) =>{
        if(e.target.files.length>0){
            setFileList([...fileList, e.target.files[0]]);
        }
    }

    const changeValue= (e) =>{
        setBoard({...board, [e.target.name]: e.target.value})
    }

    const submit = (e) =>{
        const formData = new FormData();
        formData.append("subject", board.subject);
        formData.append("content", board.content);
        formData.append("writer", board.writer);
        for(let file of fileList){
            formData.append("file", file);
        }
        axios.post(`${url}/boardWrite`, formData)
        .then(res=>{
            navigate("/");
            console.log(res.data);
        })
        .catch(err=>{
            console.log(err)
        })
    }
    return (
        <>
            <div className='header-text'>게시글 등록</div >
            <br/>
            <Form style={{divStyle}}>
                <FormGroup row>
                    <Label sm={3} for="writer">글쓴이</Label>
                    <Col sm={9}>
                        <Input type="text" name="writer" id="writer" disabled value={board.writer}/>
                    </Col>
                </FormGroup>
                <FormGroup row>
                    <Label sm={3} for="subject">제목</Label>
                    <Col sm={9}>
                        <Input type="text" name="subject" id="subject" required onChange={changeValue}/>
                    </Col>
                </FormGroup>
                <FormGroup row>
                    <Label sm={3} for="content">내용</Label>
                    <Col sm={9}>
                        <Input type="textarea" name="content" id="content" required cols="40" rows="15" onChange={changeValue}/>
                    </Col>
                </FormGroup>
                <FormGroup row>
                    <Label sm={4} for="file">이미지 파일 첨부</Label>
                    <FormGroup>
                        <img src="/plus.png" width="100px" height="100px" alt=""
                            onClick={()=>document.getElementById('file').click()}/><br/><br/>
                        <Input type="file" name="file" id="file" accept='image/*' hidden onChange={fileChange}/>
                        {
                            fileList.map((file,index)=>
                                <span key={index}>
                                    <img src={URL.createObjectURL(file)} width="100px" alt='' style={{marginRight:"10px"}}/>
                                        {(index+1)%3==0? <><br/><br/></>:''}
                                </span>)
                        }
                    </FormGroup>
                </FormGroup>
                <FormGroup style={{ marginLeft: "38%" }}>
                    <Button color="primary" onClick={submit}>등록</Button>
                    <Button style={{ marginLeft: "2%" }}>다시쓰기</Button>
                </FormGroup>
            </Form>
        </>
    )
}

export default BoardWrite;