import axios from "axios";
import { useState } from "react";
import { json, useNavigate } from "react-router";
import { Col, Button, Form, FormGroup, Label, Input, FormText, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { url } from '../config';
import {useDispatch} from 'react-redux'
const Login = () =>{
    const divStyle = {margin:"0 auto", width:"600px", border: "1px solid lightgray", borderRadius:"7px", padding: '10px'};
    const [member, setMember] = useState({id:'', password:''})
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const changeValue =(e) =>{
        setMember({...member, [e.target.name]:e.target.value});
    }
    const submit = (e) =>{
        e.preventDefault();
        axios.post(`${url}/login`, member)
        .then(res=>{
            dispatch({type:'user',payload:res.data})
            navigate("/");
        })
        .catch(err=>{
            console.log(err)
        })
    }
    return(
        <div className="route">
            <h3>로그인</h3>
            <Form>
                <FormGroup row>
                    <Label for="id" sm={3}>아이디</Label>
                    <Col sm={9}>
                        <Input tyetype="text" name="id" onChange={changeValue} />
                    </Col>
                </FormGroup>
                <FormGroup row>
                    <Label for="id" sm={3}>비밀번호</Label>
                    <Col sm={9}>
                        <Input tyetype="text" name="password" onChange={changeValue} />
                    </Col>
                </FormGroup>
                <Button onClick={submit}>로그인</Button>
            </Form>
        </div>
    )
}

export default Login;