import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Col, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import { url } from "../config";
import axios from "axios";
import { useSelector } from "react-redux";

const BoardDetail = () => {
    const divStyle = { margin: "0 auto", width: "600px", border: "1px solid lightgray", borderRadius: "7px", padding: '10px' }
    const [board, setBoard] = useState({ subject: '', content: '', writer: '', likeCount: 0 })
    const [imageList, SetImageList] = useState([]);
    const [heart, setHeart] = useState(false);
    const { num } = useParams();
    const user = useSelector(state => state.persistedReducer.user);


    useEffect(() => {
        let detailUrl = `${url}/boardDetail/${num}`;
        if (user.id != null) {
            detailUrl += `/${user.id}`;
        }
        axios.get(detailUrl)
            .then(res => {
                console.log(res)
                let resBoard = res.data.board;
                setBoard({ ...resBoard })
                setHeart(res.data.like)
                let fileNums = resBoard.fileNums.split(",");
                SetImageList([...fileNums]);

            })
            .catch(err => {
                console.log(err)
            })
    }, [])

    const like = () => {
        let id = user.id;
        setHeart(!heart);
        axios.get(`${url}/boardLike/${num}/${id}`)
    }
    return (
        <>
            <div className='header-text'>게시글 상세</div >
            <br />
            <Form style={{ divStyle }}>
                <FormGroup row>
                    <Label sm={3} for="writer">글쓴이</Label>
                    <Col sm={9}>
                        <Input type="text" name="writer" id="writer" value={board.writer} disabled />
                    </Col>
                </FormGroup>
                <FormGroup row>
                    <Label sm={3} for="subject">제목</Label>
                    <Col sm={9}>
                        <Input type="text" name="subject" id="subject" value={board.subject} disabled />
                    </Col>
                </FormGroup>
                <FormGroup row>
                    <Label sm={3} for="content">내용</Label>
                    <Col sm={9}>
                        <Input type="textarea" name="content" id="content" value={board.content} cols="40" rows="15" disabled />
                    </Col>
                </FormGroup>
                <FormGroup row>
                    <Label sm={4} for="file">이미지</Label>
                    <FormGroup>
                        {imageList.length !== 0 &&
                            imageList.map(num =>
                                <img key={num} src={`${url}/image/${num}`} width="100px" alt='' style={{ marginRight: '10px' }} />
                            )
                        }
                    </FormGroup>
                </FormGroup>
                <FormGroup style={{ marginLeft: "38%" }}>
                    {user.id === board.writer &&
                        <Button color="primary">수정</Button>
                    }&nbsp;&nbsp;
                    <Button tag="a" href="/" style={{ marginLeft: "2%" }}>목록</Button>
                    {user.id !== '' && <><img src={heart === true ? '/redheart.png' : '/blackheart.png'} alt='' onClick={like} width='30px' style={{ float: "right" }} /></>}
                    &nbsp;<span style={{ float: "right" }}>{board.likeCount}</span>
                </FormGroup>
            </Form>
        </>
    )
}
export default BoardDetail;